.page {
  display: grid;
  grid-template: auto / 1fr 400px;
  column-gap: 20px;
  min-height: 100vh;
}

.page-content {
  padding: 20px;
}

.sidebar {
  padding: 20px;
  border-left: 1px solid lightgray;
}

.input-container {
  margin: 20px 0;
}

.error {
  color: red;
  font-size: 0.8rem;
  display: block;
}


